@forward "vars";
@forward "mixins";
@forward "fonts";
@forward "settings";
@forward "fancybox";

@use "./components/header";
@use "./components/hero";
@use "./components/franchises";
@use "./components/offer";
@use "./components/question";
@use "./components/through-block";
@use "./components/uprove";
@use "./components/callback";
@use "./pages/catalog-page";
@use "./components/footer";

.breadcrumbs {
  margin-bottom: 40px;
  ul {
    display: flex;
    align-items: center;
    gap: 6px;

    li {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 14px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: #9ebefe;
      position: relative;

      &:not(:last-child) {
        &:after {
          content: "/";
          font-family: var(--font-family);
          font-weight: 500;
          font-size: 14px;
          line-height: 97%;
          letter-spacing: -0.01em;
          color: #9ebefe;
        }
      }
      &:last-child {
        color: var(--accent);
      }
      a {
        color: inherit;
        font-size: inherit;
      }
    }
  }
}

.faq {
  margin-bottom: 100px;

  &__title {
    margin-bottom: 40px;
  }

  &__items {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    align-items: flex-start;
  }

  &__item {
    padding: 17px 25px;
    border-radius: 10px;
    background: var(--blue);
    cursor: pointer;
    & > * {
      pointer-events: none;
    }
    &-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;
    }

    &-title {
      font-family: var(--font-family);
      font-weight: 500;
      font-size: 18px;
      line-height: 97%;
      letter-spacing: -0.01em;
      color: var(--accent);
    }
    &-toggler {
      transition: var(--transition);
    }

    &-footer {
      display: grid;
      grid-template-rows: 0fr;
      transition: var(--transition);
      overflow: hidden;
    }
    &-content {
      overflow: hidden;
    }
  }

  .faq__item--active {
    .faq__item-toggler {
      transform: rotate(180deg);
    }
    .faq__item-footer {
      grid-template-rows: 1fr;
      margin-top: 20px;
    }
  }
}
