  font-size: 12px;
  line-height: 136%;
  letter-spacing: -0.02em;
  color: var(--gray);
  display: block;
  padding-left: 28px;
}
.callback .form-cb .policy label:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  border: 1px solid #c1c1c1;
  width: 17px;
  height: 17px;
  display: grid;
  place-items: center;
  transition: var(--transition);
}
.callback .form-cb .policy input:checked + label:before {
  content: "✓";
  color: var(--white);
  background: var(--accent);
  border-color: transparent;
}
.callback .form-cb__btn {
  background: var(--accent);
  color: var(--white);
  padding-block: 17px;
}
@media (any-hover: hover) {
  .callback .form-cb__btn:hover {
    background: var(--hovered);
  }
}
.callback .form-cb__btn:active {
  background: var(--active);
}

