.faq {
  margin-bottom: 100px;
}
.faq__title {
  margin-bottom: 40px;
}
.faq__items {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  align-items: flex-start;
}
.faq__item {
  padding: 17px 25px;
  border-radius: 10px;
  background: var(--blue);
  cursor: pointer;
}
.faq__item > * {
  pointer-events: none;
}
.faq__item-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
.faq__item-title {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 97%;
  letter-spacing: -0.01em;
  color: var(--accent);
}
.faq__item-toggler {
  transition: var(--transition);
}
.faq__item-footer {
  display: grid;
  grid-template-rows: 0fr;
  transition: var(--transition);
  overflow: hidden;
}
.faq__item-content {
  overflow: hidden;
}
.faq .faq__item--active .faq__item-toggler {
  transform: rotate(180deg);
}
.faq .faq__item--active .faq__item-footer {
  grid-template-rows: 1fr;
  margin-top: 20px;
}